	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"OpenOffice","date":"11 Oct 2002","slug":"/2002-10-11-openoffice/","tags":"Y2002"},"html":"<p>My big accomplishment for this week was getting <a href=\"http://www.openoffice.org/\">OpenOffice</a> compiled and installed on FreeBSD. It was an accomplishment only in that it took nearly a day to download (130 MB over ISDN) and another day to compile (update to <a href=\"http://www.freebsd.org\">FreeBSD 4.7</a> first). Not that I have any real use for an office package, but sometimes you can't avoid having to use one. At work we are going to be cutting way back on the number of MS Office licenses (I think down to 20; we have 130 employees). The rest of us will have OpenOffice.</p>"},"next":{"fields":{"title":"Monsters","date":"11 Oct 2002","slug":"/2002-10-11-monsters/"}},"previous":{"fields":{"title":"Seventy-five Percent","date":"12 Oct 2002","slug":"/2002-10-12-seventy-five-percent/"}}},{"node":{"fields":{"title":"Monsters","date":"11 Oct 2002","slug":"/2002-10-11-monsters/","tags":"Y2002"},"html":"<p>Christopher is starting to become frightened of monsters. For example, I had thought that <a href=\"http://us.imdb.com/Title?0198781\">Monsters, Inc.</a> would be a cute video for us to watch together, but after the first two minutes, he demanded that I turn it off. Since then, the couple of toys he has from that movie have been at the bottom the the toy favorite list (as in he throws them down the stairs everytime he notices them). And yesterday's <em>Sesamstrasse</em> had a theme of scary shadows that look like monsters, and the theme did not please him at all. He was impressed by on clip where the monster in Ernie's bedroom turned out to be a quilt. That became his mantra for the next hour. \"Monster. Quilt. Monster. Quilt.\"</p>\n<p>I was reminded of monsters by today's <a href=\"http://www.lileks.com/bleats/archive/02/1002/100202.html#101102\">Bleat</a>, which also provides an accurate description of what happens to a father's psyche when the mother is out of town for several days.</p>"},"next":{"fields":{"title":"Extra Dry","date":"11 Oct 2002","slug":"/2002-10-11-extra-dry/"}},"previous":{"fields":{"title":"OpenOffice","date":"11 Oct 2002","slug":"/2002-10-11-openoffice/"}}},{"node":{"fields":{"title":"Extra Dry","date":"11 Oct 2002","slug":"/2002-10-11-extra-dry/","tags":"Y2002"},"html":"<p>Quote of the day, seen at <a href=\"http://www.couchblog.de/nico/archives/000657.php\">couchblog</a>: 'It can't be <em>that</em> tough to catch an Arab pulling a dialysis machine behind him!\"</p>"},"next":{"fields":{"title":"What Would Jesus Do?","date":"10 Oct 2002","slug":"/2002-10-10-what-would-jesus-do/"}},"previous":{"fields":{"title":"Monsters","date":"11 Oct 2002","slug":"/2002-10-11-monsters/"}}}],"pathPrefix":"","first":false,"last":false,"index":833,"pageCount":1101}}

